.wrap {
  z-index: 5000;
  position: fixed;
  left: 50%;
  // top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  color: #fff;
  font-size: 28px;
  text-align: center;
  background: rgba(17, 17, 17, 0.7);
  opacity: 0;
  padding: 10px 20px;
  animation-name: fade;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-duration: 0.7s;
}

@keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

