@import "../../styles/variables.scss";

.nmd {
  font-size: 28px;
  color: $fontColor;

  justify-content: center;
  padding: 20px;
  display: none;
  &.show {
    display: flex;
  }
  .wrap {
    width: 300px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    i {
      width: 80px;
      height: 1px;
      display: inline-block;
      background: $fontColor;
    }
  }
}
