@import "../../styles/variables.scss";

.container {
  // height: 90vh;
  background: $bgGray;

  .bind_tips {
    font-size: 30px;
    padding: 10px 20px;
    background: #fef6e4;
    // color:$fontColor;
    a {
      color: $mainColor;
    }
    i {
      display: inline-block;
      width: 24px;
      height: 24px;
      margin-left: 15px;
      @include bgImg("../../images/2-user/user_close@3x.png");
    }
  }

  .infoWrap {
    background: #fff;
    height: 24vh;
    margin-bottom: 20px;

    .baseInfo {
      box-sizing: border-box;
      height: 12vh;
      display: flex;
      align-items: center;
      font-size: 30px;
      padding: 0 20px;
      border-bottom: 1px solid $borderColor;
      .img_box {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        margin-right: 30px;
        overflow: hidden;
        .avatar {
          width: 100%;
          height: 100%;
        }
      }

      .name {
        margin-right: 40px;
      }
    }

    .moneyInfo {
      box-sizing: border-box;
      height: 12vh;
      display: flex;
      align-items: center;
      font-size: 30px;

      div + div {
        &::after {
          content: "";
          width: 1px;
          height: 50px;
          background: $borderColor;
          position: absolute;
          left: 0;
        }
      }

      div {
        position: relative;
        display: flex;
        flex: 1;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        p {
          margin-top: 10px;
          color: $mainColor;
        }
      }
    }
  }

  .featureList {
    a {
      display: block;
      background: #fff;

      &:nth-child(4) {
        margin-bottom: 20px;

        div {
          border: none;
        }
      }

      &:nth-child(6) {
        div {
          border: none;
        }
      }
    }

    .featureItem {
      box-sizing: border-box;
      height: 7vh;
      display: flex;
      align-items: center;
      font-size: 28px;
      padding-left: 20px;

      div {
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-left: 20px;
        border-bottom: 1px solid $borderColor;
        padding-right: 20px;

        .iconRight {
          width: 32px;
          height: 32px;
          @include bgImg("../../images/main/right.png");
        }
      }

      @for $i from 1 through 6 {
        .icon#{$i} {
          width: 54px;
          height: 54px;
          @include bgImg("../../images/2-user/user_icon_#{$i}@3x.png");
        }
      }
    }
  }

  button {
    width: 600px;
    height: 80px;
    position: relative;
    left: 50%;
    margin-top: 50px;
    margin-bottom: 50px;
    transform: translateX(-50%);
    background: $mainColor;
    color: #fff;
    border: none;
    border-radius: 20px;
    font-size: 30px;
  }
}
