@import "../../styles/variables.scss";

.container {
  box-sizing: border-box;
  min-height: 94vh;
  background: $bgGray;
  padding: 50px;

  input {
    width: 100%;
    height: 50px;
    padding: 10px;
    margin-bottom: 50px;
    font-size: 30px;
    border-radius: 10px;
    border: 1px solid $borderColor;
    box-shadow: none;
    -webkit-appearance: none;
  }

  p {
    font-size: 22px;
    color: $fontColor;
    margin-bottom: 30px;
  }

  button {
    width: 100%;
    height: 90px;
    background: $mainColor;
    border: none;
    font-size: 30px;
    color: #fff;
    margin-top: 50px;
    border-radius: 45px;
  }

  .img_code,
  .phone_code {
    display: flex;
    justify-content: space-between;

    input {
      width: 350px;
    }
    .img_box {
      width: 230px;
      height: 70px;
      img {
        width: 100%;
        height: 100%;
      }
    }

    div {
      width: 230px;
      height: 70px;
      font-size: 30px;
      text-align: center;
      line-height: 70px;
      color: $mainColor;
      border: 1px solid $mainColor;
      border-radius: 15px;
      span {
        display: inline-block;
        width: 100%;
        height: 100%;
      }
    }
    .coutn_down {
      color: $fontColor;
      border-color: $borderColor;
    }
  }
}
