@import '../../styles/variables.scss';

.notice {
    box-sizing: border-box;
    // height: 60px;
    height: 5vh;
    // line-height: 60px;
    line-height: 5vh;
    padding-left: 60px;
    padding-right: 10px;
    box-shadow: $shadow;
    position: relative;
    border-radius: 10px;
    margin-bottom: 20px;

    i {
        width: 39px;
        height: 31px;
        z-index: 100;
        position: absolute;
        left: 10px;
        top: 50%;
        transform: translateY(-50%);
        @include bgImg('../../images/1-home/home_horn@3x.png');
    }

    .loop_wrap {
        display: flex;
        overflow: hidden;


        .loop_content {
            display: flex;
            position: relative;

            div {
                white-space: nowrap;
                font-size: 22px;
                color:$fontColor;
            }
        }
    }
}