@import "../../styles/variables.scss";

.header_icon {
  display: inline-block;
  width: 33px;
  height: 38px;
  @include bgImg("../../images/main/main_refresh@3x.png");
}

.task_list {
  box-sizing: border-box;
  min-height: 94vh;
  background: $bgGray;
  padding: 20px;

  .task_item {
    padding: 0;
    margin-bottom: 15px;

    &.task_static {
      .des {
        padding: 2px 6px;
        color: $mainColor;
        border: 1px solid $mainColor;
      }

      .task_type {
        padding: 0;
      }
    }

    &.task_empty {
      .right_msg {
        color: $fontColor !important;
      }
    }

    &.img_blur {
      .img_box {
        overflow: hidden;
      }

      .avatar {
        filter: blur(12px);
      }
    }

    .task_type {
      font-size: 34px;
      padding: 0 0 15px 20px;
    }

    .task_content {
      display: flex;
      background: #fff;
      padding: 20px;
      border-radius: 10px;
      position: relative;
      font-size: 30px;
      margin-bottom: 15px;

      .img_box {
        width: 100px;
        height: 100px;
        border-radius: 15px;
        margin-right: 20px;
        overflow: hidden;
        .avatar {
          width: 100%;
          height: 100%;
        }
      }

      .info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .des {
          font-size: 22px;
          display: flex;

          .des_a {
            color: $fontColor;
            border: 1px solid $fontColor;
            padding: 0 5px;
            margin-right: 15px;
          }

          .des_b {
            color: $mainColor;
            border: 1px solid $mainColor;
            padding: 0 3px;
          }
        }
      }

      .right_msg {
        position: absolute;
        right: 20px;
        top: 50%;
        font-size: 34px;
        color: $mainColor;
        font-weight: bold;
        transform: translateY(-50%);
      }
    }
  }
}
