@import "../../styles/variables.scss";

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  .icon {
    width: 300px;
    height: 300px;
    margin-top: 200px;
    margin-bottom: 50px;
    @include bgImg('../../images/main/main_notRegister@3x.png');
  }
  .des {
    font-size: 30px;
    color: $fontColor;
    margin-bottom: 100px;
  }
  button {
    width: 300px;
    height: 80px;
    background: $mainColor;
    color: #fff;
    font-size: 30px;
    border: none;
    border-radius: 40px;
  }
}
