@import '../../styles/variables.scss';

.nav {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    // height: 150px;
    height: 13vh;
    padding: 30px;
    box-shadow: $shadow;
    border-radius: 20px;
    margin-bottom: 20px;

    .link {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-decoration: none;
        color: $fontColor;
        font-size: 26px;

        i {
            width: 70px;
            height: 71px;
            margin-bottom: 10px;

            &.benefit_icon {
                @include bgImg('../../images/1-home/shoppingPage.png');
            }

            &.mentoring_icon {
                @include bgImg('../../images/1-home/masterPage.png');
            }

            &.sort_icon {
                @include bgImg('../../images/1-home/rankPage.png');
            }

            &.account_icon {
                @include bgImg('../../images/1-home/userPage.png');
            }
        }
    }
}