@import '../../styles/variables.scss';

.container {
    background: $bgGray;
    font-size: 0;

    .poster {
        width: 100%;
        height: 250px;
        @include bgImg('../../images/4-master/master_header@3x.png')
    }

    .info {
        box-sizing: border-box;
        display: flex;
        height: 15vh;
        margin-bottom: 20px;
        background: #fff;

        .info_bepowder,
        .info_earning {
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            :nth-child(1) {
                font-size: 34px;
                color: $fontColor;
                margin-bottom: 10px;
            }

            :nth-child(2) {
                font-size: 30px;
                color: #EA8E33;
            }
        }
    }

    .rule {
        box-sizing: border-box;
        height: 25vh;
        background: #fff;
        margin-bottom: 20px;

        .title_box {
            display: flex;
            color: $fontColor;
            justify-content: center;
            align-items: center;
            padding: 20px 0;

            i {
                width: 100px;
                border-bottom: 1px solid #DEDEDE;
            }

            span {
                font-size: 34px;
                padding: 0 50px;
            }
        }

        .rule_list {
            font-size: 30px;
            color: $fontColor;
            padding: 20px;
            margin: 0;

            .rule_item {
                display: flex;
                align-items: center;
                margin-bottom: 20px;

                &::before {
                    content: '';
                    display: inline-block;
                    width: 10px;
                    height: 10px;
                    border-radius: 50%;
                    background: $fontColor;
                    margin-right: 20px;
                }
            }
        }
    }

    .qr_box {
        box-sizing: border-box;
        height: calc(54vh - 250px - 40px);
        padding: 40px 80px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: #fff;
        i {
            display: inline-block;
            width: 200px;
            height: 283px;
        }
        .qr_share {
            i {
                @include bgImg('../../images/4-master/master_share_img@3x.png')
            }
        }
        .qr_recruit {
            i {
                @include bgImg('../../images/4-master/master_share_code@3x.png')
            }
        }
    }
}