$mainColor:#FF5447;
$shadow:0px 0px 20px -10px #FF5447;
$fontColor:#7C7C7C;
$bgGray:#F5F5F5;
$borderColor:#dedede;

//背景图
@mixin bgImg($url) {
    background-image: url($url);
    background-size: 100vw 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}