@import '../../styles/variables.scss';

.container {
    box-sizing: border-box;
    padding: 0 40px;
    min-height: 94vh;
    .balance {
        padding: 40px 0;
    }

    .select_text {
        font-size: 28px;
        color: $fontColor;
        margin-bottom: 30px;
    }

    .money_list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: 30px;

        .money_item {
            box-sizing: border-box;
            width: 315px;
            height: 75px;
            // background: $mainColor;
            font-size: 30px;
            color: $fontColor;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 40px;
            border: 1px solid $borderColor;
        }

        .money_active {
            background: $mainColor;
            color: #fff;
            border: none;
        }
    }

    .to_wx {
        display: flex;
        align-items: center;
        position: relative;

        .wx_icon {
            width: 56px;
            height: 56px;
            margin-right: 20px;
            font-size: 0;
            @include bgImg('../../images/2-user/user_withdrawWay_wx@3x.png')
        }

        span {
            font-size: 28px;
            color: $fontColor;
        }

        .select_icon {
            width: 35px;
            height: 35px;
            position: absolute;
            right: 0;
            font-size: 0;
            @include bgImg('../../images/2-user/user_selected@3x.png')
        }

    }

    button {
        width: 100%;
        height: 80px;
        background: $mainColor;
        color: #fff;
        border: none;
        font-size: 32px;
        margin-top: 150px;
        border-radius: 40px;
    }
}