@import '../../styles/variables.scss';

.container {
    z-index: 2000;
    position: absolute;
    top: -6vh;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);

    .check_box {
        box-sizing: border-box;
        width: 550px;
        height: 400px;
        position: relative;
        top: -200px;
        left: -275px;
        padding: 20px 40px;
        
        border-radius: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 44vh;
        margin-left: 50vw;
        background: #fff;

        /*关键帧名称*/
        animation-name: rebound;
        /*动画的速度曲线*/
        animation-timing-function: ease-in-out;
        /*动画播放的次数*/
        // animation-iteration-count: infinite;
        /*动画所花费的时间*/
        animation-duration: .3s;


        @keyframes rebound {
            0% {
                transform: scale(1);
            }

            50% {
                transform: scale(.9);
            }

            75% {
                transform: scale(1.1);
            }

            100% {
                transform: scale(1);
            }
        }


        b {
            font-size: 32px;
            margin-bottom: 20px;
        }
        .phone_icon {
            width: 120px;
            height: 120px;
            margin-bottom: 30px;
            @include bgImg('../../images/main/main_codeVerify@2x.png');
        }

        .close_icon {
            width: 35px;
            height: 35px;
            position: absolute;
            top: 20px;
            right: 20px;
            @include bgImg('../../images/main/main_close@2x.png');
        }

        p {
            color: $fontColor;
            font-size: 28px;
            text-align: center;
            margin-bottom: 30px;
        }

        .code_ipt{
            box-sizing: border-box;
            width: 100%;
            height: 70px;
            font-size: 28px;
            padding: 10px;
            border-radius: 10px;
            border: 1px solid $borderColor;
            margin-bottom: 50px;
        }
        
        .sumbit {
            width: 250px;
            height: 60px;
            font-size: 30px;
            background: $mainColor;
            color: #fff;
            border-radius: 40px;
        }
    }
}