@import "../../styles/variables.scss";

.container {
  box-sizing: border-box;
  min-height: 94vh;
  padding: 20px;
  background: $bgGray;

  .top_info {
    background: #fff;
    box-sizing: border-box;
    height: 10vh;
    padding: 20px;
    border-radius: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

    .info_left {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .info_text {
        font-size: 30px;
        color: $fontColor;
      }

      .info_last_time {
        font-size: 26px;
        color: $mainColor;
      }
    }

    .info_income {
      font-size: 34px;
      color: $mainColor;
      font-weight: bold;
    }
  }

  .task_step {
    box-sizing: border-box;
    // height: calc(84vh - 60px);
    background: #fff;
    border-radius: 15px;
    padding: 20px 20px 0 20px;

    .step_box {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 0;
      color: $fontColor;
      border-bottom: 1px solid $borderColor;
      padding: 30px 0;

      span {
        color: $mainColor;
        font-size: 28px;
      }
      .img_box {
        width: 160px;
        height: 160px;
        border-radius: 40px;
        margin-bottom: 20px;
        overflow: hidden;
        .task_img {
          width: 100%;
          height: 100%;
        }
      }

      .step_btn {
        width: 400px;
        height: 70px;
        border-radius: 40px;
        line-height: 70px;
        text-align: center;
        margin-top: 30px;
        background: $mainColor;
        color: #fff;
        font-size: 28px;
      }

      .step_des_a,
      .step_des_b,
      .step_des_c {
        margin-bottom: 10px;
        font-size: 26px;

        .bg_red {
          display: inline-block;
          color: #fff;
          background: $mainColor;
          padding: 0px 15px;
          margin-right: 10px;
        }
      }
    }

    .step_one {
      padding-top: 0;
    }

    .step_three {
      border-bottom: none;
    }
  }
}
