@import "../../styles/variables.scss";

.container {
  box-sizing: border-box;
  padding: 50px 0;
  min-height: 94vh;
  background: #fbbb57;
  display: flex;
  flex-direction: column;
  align-items: center;
  // justify-content: space-between;

  .des {
    width: 400px;
    font-size: 30px;
    color: #fff;
    text-align: center;
    margin-bottom: 100px;
  }

  .img_box {
    display: inline-block;
    width: 558px;
    height: 558px;
    margin-bottom: 200px;
    .qr {
      width: 100%;
      height: 100%;
    }
  }

  a {
    width: 450px;
    height: 100px;
    text-align: center;
    line-height: 100px;
    border: 1px solid #fff;
    font-size: 30px;
    color: #fff;
    background: none;
    border-radius: 50px;
  }
}
