@import "../../styles/variables.scss";

.container {
  min-height: 94vh;
  background: $bgGray;

  .play_list {
    background: #fff;

    .play_items {
      .item_overview {
        background: $bgGray;
        display: flex;
        justify-content: space-between;
        font-size: 22px;
        padding: 5px 40px;
        color: $fontColor;
        border-bottom: 1px solid $borderColor;
      }

      .block_list {
        padding-left: 40px;
        border-bottom: 1px solid $borderColor;

        .block_item + .block_item {
          border-top: 1px solid $borderColor;
        }

        .block_item {
          display: flex;
          justify-content: space-between;
          align-items: center;
          // border-bottom: 1px solid $borderColor;
          padding: 20px 40px 20px 0;

          .block_left {
            display: flex;

            img {
              width: 80px;
              height: 80px;
              margin-right: 40px;
              border-radius: 50%;
            }

            .block_info {
              font-size: 28px;
              display: flex;
              flex-direction: column;
              justify-content: space-between;
              .block_name {
                max-width: 400px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
              }
              .block_date {
                font-size: 22px;
                color: $fontColor;
              }
            }
          }

          .block_income {
            font-size: 34px;
            color: $mainColor;
          }
        }
      }
    }
  }
}
