@import "../../styles/variables.scss";

.container {
  min-height: 94vh;
  display: flex;
  align-items: center;

  .share_box {
    width: 650px;
    border-radius: 30px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #fff;
    background: linear-gradient(#fd8b39, #f07038);

    .img_box_a {
      width: 150px;
      height: 150px;
      border-radius: 50%;
      margin-bottom: 30px;
      margin-top: -75px;
      overflow: hidden;
      .avatar {
        width: 100%;
        height: 100%;
      }
    }

    .name {
      font-size: 30px;
      margin-bottom: 10px;
    }

    .id {
      font-size: 30px;
      margin-bottom: 30px;
    }

    .img_box_b {
      width: 300px;
      height: 300px;
      border-radius: 25px;
      margin-bottom: 10px;
      overflow: hidden;
      .qr {
        width: 100%;
        height: 100%;
      }
    }

    .text1 {
      font-size: 22px;
      margin-bottom: 30px;
    }

    .share_link {
      box-sizing: border-box;
      width: 520px;
      padding: 10px 30px;
      word-break: break-all;
      text-align: center;
      border: 1px dashed #fff;
      font-size: 26px;
      border-radius: 15px;
      margin-bottom: 10px;
      user-select: none;
    }

    .text2 {
      width: 520px;
      font-size: 22px;
      text-align: center;
      margin-bottom: 30px;
    }

    button {
      box-sizing: border-box;
      width: 400px;
      padding: 20px 0;
      font-size: 34px;
      color: #fff;
      text-align: center;
      border: 1px solid #fff;
      background: none;
      border-radius: 100px;
      margin-bottom: 30px;
    }
  }
}
