:global {
    * {
        padding: 0;
        margin: 0;
    }

    body {
        position: sticky;
        font-family: "Microsoft YaHei", SimHei, "\9ED1\4F53", "Apple LiGothic Medium", "\82F9\679C\4E3D\4E2D\9ED1", SimSun, "\5b8b\4f53", sans-serif;
    }

    li {
        list-style: none;
    }

    img {
        display: block;
        border: none;
    }


    a {
        text-decoration: none;
        color: black;
        font-weight: normal;
    }

    h1 {
        font-weight: normal;
    }

    i {
        font-style: normal;
    }

    button,
    input,
    textarea {
        outline: none;
        box-shadow: none;
        background: none;
        border: none;
        -webkit-appearance: none;
    }

    .swiper-pagination-bullet-active {
        background: #fff;
    }

    .mt6 {
        margin-top: 6vh;
    }

    .alert-enter {
        opacity: 0;
        transform: scale(0.9);
    }

    .alert-enter-active {
        opacity: 1;
        transform: translateX(0);
        transition: opacity 300ms, transform 300ms;
    }

    .alert-exit {
        opacity: 1;
    }

    .alert-exit-active {
        opacity: 0;
        transform: scale(0.9);
        transition: opacity 300ms, transform 300ms;
    }
}