@import "../../styles/variables.scss";

.container {
  background: $bgGray;

  .tabs {
    display: flex;
    background: #fff;
    border-bottom: 1px solid $borderColor;

    .tab_item {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $fontColor;
      padding: 30px 0;
      font-size: 34px;
    }

    .tab_active {
      color: $mainColor;
      border-bottom: 1px solid $mainColor;
    }

    div + div {
      position: relative;

      &::before {
        content: "";
        border-left: 1px solid $borderColor;
        height: 40px;
        position: absolute;
        left: 0;
      }
    }
  }

  .sort_date {
    padding: 10px;
    color: $fontColor;
    font-size: 26px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: $bgGray;
    border-bottom: 1px solid $borderColor;
  }

  .sort_list {
    background: #fff;
    margin: 0;
    padding: 0;
    font-size: 30px;

    .list_item {
      padding: 20px;
      display: flex;
      justify-content: space-between;

      .list_left {
        display: flex;
        align-items: center;
        align-items: center;

        .ids {
          width: 50px;
          height: 50px;
          border-radius: 50%;
          color: #fff;
          background: #999;
          display: flex;
          justify-content: center;
          align-items: center;
          margin-right: 20px;
        }

        .one_bgc {
          background: $mainColor;
        }

        .two_bgc {
          background: #fbbb57;
        }

        .three_bgc {
          background: #fed661;
        }

        .img_box {
          width: 80px;
          height: 80px;
          border-radius: 50%;
          margin-right: 20px;
          overflow: hidden;
          .avatar {
              width: 100%;
              height: 100%;
          }
        }
      }

      .list_right {
        color: $fontColor;

        .list_right_top {
          text-align: right;
        }
      }
    }
  }
}
