@import "../../styles/variables.scss";

.header_icon {
  display: inline-block;
  width: 33px;
  height: 38px;
  @include bgImg("../../images/main/main_refresh@3x.png");
}

.container {
  min-height: 94vh;
  background: $bgGray;

  .play_list {
    background: #fff;

    .play_items {
      .item_overview {
        background: $bgGray;
        display: flex;
        justify-content: space-between;
        font-size: 22px;
        padding: 5px 40px;
        color: $fontColor;
        border-bottom: 1px solid $borderColor;
      }

      .block_list {
        padding-left: 40px;
        border-bottom: 1px solid $borderColor;

        .block_item + .block_item {
          border-top: 1px solid $borderColor;
        }
      }
    }
  }
}

.block_item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  // border-bottom: 1px solid $borderColor;
  padding: 20px 40px 20px 0;
  &.md {
    border-bottom: 20px solid $bgGray;
    padding-left: 20px;
  }

  &.sm {
    .block_left {
      i {
        width: 60px;
        height: 60px;
        margin-right: 40px;
        @include bgImg("../../images/2-user/user_tudi_detail@2x.png");
      }
    }
  }

  .block_left {
    display: flex;

    .img_box {
      width: 80px;
      height: 80px;
      margin-right: 40px;
      border-radius: 50%;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
    }

    .block_info {
      font-size: 28px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .block_name {
        max-width: 400px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .block_date {
        font-size: 22px;
        color: $fontColor;
      }
    }
  }

  .block_income {
    font-size: 34px;
    color: $mainColor;
  }
}
