@import "../../styles/variables.scss";

.wrap {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  height: 25.7vh;
  margin-bottom: 20px;
  .left_box {
    width: calc(50% - 10px);
  }

  .right_box {
    display: flex;
    width: calc(50% - 10px);
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    font-size: 0;
    a {
      height: calc(50% - 10px);
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}

.content {
  box-sizing: border-box;
  padding: 30px 20px;
  color: #fff;
  width: 100%;
  height: 100%;
  box-shadow: $shadow;
  border-radius: 10px;
  text-align: center;

  .title {
    font-size: 36px;
    margin-bottom: 10px;
    text-align: left;
  }
  .des {
    font-size: 28px;
    text-align: left;
  }
  button {
    display: inline-block;
    width: 90%;
    height: 80px;
    background: #fff;
    color: $mainColor;
    font-size: 32px;
    border-radius: 40px;
    margin-top: 50px;
  }

  &.a {
    @include bgImg("../../images/1-home/home_btn_fast@3x.jpg");
  }
  &.b {
    @include bgImg("../../images/1-home/11563501803_.pic.jpg");
  }
  &.c {
    @include bgImg("../../images/1-home/31563501805_.pic.jpg");
  }
}
