@import "../../styles/variables.scss";

.info {
  box-sizing: border-box;
  // height: 250px;
  height: 22vh;
  background: $mainColor;
  border-radius: 20px;
  box-shadow: $shadow;
  color: #fff;
  padding: 30px 0 30px 30px;
  margin-bottom: 20px;
  font-size: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .info_top {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .info_top_right {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .img_box {
        width: 120px;
        height: 120px;
        border-radius: 50%;
        overflow: hidden;
        .info_avatar {
          width: 100%;
          height: 100%;
        }
      }

      .info_name_box {
        display: flex;
        flex-direction: column;
        margin-left: 30px;
      }
    }

    .withdraw_btn {
      width: 150px;
      height: 50px;
      line-height: 50px;
      background: #faa014;
      border-radius: 25px 0 0 25px;
      padding-left: 25px;
      color: #fff;
    }
  }

  .info_money {
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    padding-left: 30px;
    padding-right: 60px;
  }
}
