@import '../../styles/variables.scss';

.header {
    width: 100%;
    box-sizing: border-box;
    height: 6vh;
    padding: 20px 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: $mainColor;
    color: #fff;
    font-size: 34px;
    position: fixed;
    top: 0;
    z-index: 100;

    .back {
        width: 22px;
        height: 38px;
        @include bgImg('../../images/main/main_back@3x.png');

        &::after {
            content: '';
            display: inline-block;
            width: 60px;
            height: 38px;
        }
    }
    .right_content {
        font-size: 30px;
    }
}