@import "../../styles/variables.scss";

.container {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 3000;
  display: none;

  &.show {
    display: block;
    animation: fib 0.4s;
    @keyframes fib {
      0% {
        opacity: 0;
      }
      50% {
        opacity: 0.3;
      }
      100% {
        opacity: 1;
      }
    }
    .conten_wrap {
      animation: fi 0.4s;
      @keyframes fi {
        0% {
          opacity: 0;
          top: 0;
        }
        50% {
          opacity: 0.3;
        }
        100% {
          opacity: 1;
          top: 50%;
        }
      }
    }
  }

  .conten_wrap {
    width: 500px;
    height: 500px;
    border-radius: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -250px;
    margin-top: -250px;
    z-index: 5000;
    .bg_img {
      position: relative;
      width: 500px;
      height: 500px;
      border-radius: 20px;
      @include bgImg('../../images/main/main_getMoneyImg@3x.png');
      b {
        position: absolute;
        bottom: 80px;
        left: 50%;
        transform: translateX(-50%);
        font-size: 50px;
        color: $mainColor;
        span {
          font-weight: normal;
          font-size: 35px;
          margin-left: 10px;
        }
      }
    }
  }
}
