@import '../../styles/variables.scss';

.container {
    min-height: 94vh;
    background: $bgGray;

    .income_list {
        background: #fff;
        padding-left: 40px;
        border-bottom: 1px solid $borderColor;

        .income_item {
            padding: 20px 40px 20px 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 30px;
            border-bottom: 1px solid $borderColor;
            &:last-child {
                border-bottom: none;
            }
            .type_name {
                margin-bottom: 5px;
            }
            .date {
                font-size: 22px;
                color: $fontColor;
            }
            .item_right {
                color:$fontColor;
                font-size: 34px;
            }
        }
    }
}