@import "../../styles/variables.scss";

// .select_share {
.shade {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000;
  opacity: 0.3;
  z-index: 3000;
}

.share_type {
  box-sizing: border-box;
  background: #e8f0f4;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 4000;
  max-height: 300px;
  .text {
    display: inline-block;
    font-size: 30px;
    padding: 20px 0;
    color: #3d3f3f;
  }

  .type_icons {
    display: flex;
    justify-content: center;
    font-size: 26px;
    color: $fontColor;
    div + div {
      margin-left: 50px;
    }
    div {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    i {
      display: inline-block;
      width: 90px;
      height: 90px;
      background: #fff;
      border-radius: 50%;
      margin-bottom: 10px;
    }

    .wx {
      i {
        @include bgImg('../../images/main/wx.jpg');
      }
    }
    .pyq {
      i {
        @include bgImg('../../images/main/pyq.jpg');
      }
    }
  }

  .cancel {
    padding: 15px;
    font-size: 28px;
    background: #f5fafd;
    color: $fontColor;
    margin-top: 10px;
  }
}
// }

:global {
  // 动画
  .share-enter {
    opacity: 0;
  }
  .share-enter-active {
    opacity: 1;
    transition: opacity 200ms;
  }
  .share-exit {
    opacity: 1;
  }
  .share-exit-active {
    opacity: 0;
    transition: opacity 200ms;
  }

  .share-type-enter {
    max-height: 0;
  }
  .share-type-enter-active {
    max-height: 300px;
    transition: max-height 200ms;
  }
  .share-type-exit {
    max-height: 300px;
  }
  .shshare-typeare-exit-active {
    max-height: 0;
    transition: max-height 200ms;
  }
}
